@import '../../common/brand-colors.scss';

.wp-block-zolo-social-share {
    --zolo-item-grid-gap: 10px;
    --zolo-icon-size: 1em;
    --zolo-button-text-color: #fff;
    --zolo-button-padding: 10px 20px;
    --zolo-button-gap: 10px;
    --zolo-item-border-width: 2px;
    --zolo-item-border-style: solid;
    --zolo-icon-width: 40px;
    --zolo-icon-height: 40px;
    --zolo-button-text-padding: 0 12px;

    gap: var(--zolo-item-grid-gap);
    &.flex {
        display: flex;
        flex-wrap: wrap;
    }
    &.grid {
        display: grid;
    }
    .zolo-social-item {
        border-radius: var(--zoloblocks-button-border-radius);
        font-size: 16px;
        display: flex;
        align-items: center;
        justify-content: center;
        box-sizing: border-box;
        text-decoration: none;
        background-color: var(--zoloblocks-brand-color);
        transition: all 0.2s ease;
        cursor: pointer;
        svg {
            width: var(--zolo-icon-size);
            height: var(--zolo-icon-size);
            fill: var(--zoloblocks-brand-button-color);
            display: block;
            transition: all 0.2s ease;
        }
        &:hover {
            background-color: var(--zoloblocks-brand-color-hover);
        }
        &.original {
            &.facebook,
            &.facebook-f,
            &.facebook-square,
            &.fb {
                background-color: var(--facebook);
                border-color: var(--facebook);
                &:hover {
                    background-color: var(--facebook-hover);
                }
                .zolo-social-icon {
                    background-color: var(--facebook);
                }
            }
            &.twitter,
            &.twitter-square {
                background-color: var(--twitter);
                border-color: var(--twitter);
                &:hover {
                    background-color: var(--twitter-hover);
                }
                .zolo-social-icon {
                    background-color: var(--twitter);
                }
            }
            &.email {
                background-color: var(--email);
                border-color: var(--email);
                &:hover {
                    background-color: var(--email-hover);
                }
                .zolo-social-icon {
                    background-color: var(--email);
                }
            }
            &.linkedin,
            &.linkedin-in {
                background-color: var(--linkedin);
                border-color: var(--linkedin);
                &:hover {
                    background-color: var(--linkedin-hover);
                }
                .zolo-social-icon {
                    background-color: var(--linkedin);
                }
            }
            &.viber,
            &.viber-square {
                background-color: var(--viber);
                border-color: var(--viber);
                &:hover {
                    background-color: var(--viber-hover);
                }
                .zolo-social-icon {
                    background-color: var(--viber);
                }
            }
            &.pinterest,
            &.pinterest-p,
            &.pinterest-square {
                background-color: var(--pinterest);
                border-color: var(--pinterest);
                .zolo-social-icon {
                    background-color: var(--pinterest);
                }
                &:hover {
                    background-color: var(--pinterest-hover);
                }
            }
            &.hackernews,
            &.hackernews-square {
                background-color: var(--hacker-news);
                border-color: var(--hacker-news);
                .zolo-social-icon {
                    background-color: var(--hacker-news);
                }
                &:hover {
                    background-color: var(--hacker-news-hover);
                }
            }
            &.whatsapp,
            &.whatsapp-square {
                background-color: var(--whatsapp);
