                }
                &:hover {
                    background-color: var(--xing-hover);
                }
            }
        }
    }

    &.preset-1,
    &.preset-2 {
        .zolo-social-icon {
            background-color: transparent !important;
            background: transparent;
        }
    }
    &.preset-1,
    &.preset-2,
    &.preset-3 {
        .zolo-social-item {
            color: var(--zolo-button-text-color);
        }
    }

    &.preset-1 {
        .zolo-social-item {
            padding: var(--zolo-button-padding);
            gap: var(--zolo-button-gap);
        }
    }

    &.preset-2 {
        .zolo-social-item {
            --zolo-button-padding: 10px;
            padding: var(--zolo-button-padding);
            gap: var(--zolo-button-gap);
        }
    }

    &.preset-3 {
        .zolo-social-item,
        .zolo-social-item.original {
            background-color: transparent;
            background: transparent;
            border-width: var(--zolo-item-border-width);
            border-style: var(--zolo-item-border-style);
            .zolo-social-icon {
                width: var(--zolo-icon-width);
                height: var(--zolo-icon-width);
                display: flex;
                align-items: center;
                justify-content: center;
            }
            .zolo-social-text {
                padding: var(--zolo-button-text-padding);
            }
        }

        .zolo-social-icon {
            height: 40px;
            width: 40px;
            display: flex;
            align-items: center;
            justify-content: center;
        }

        .zolo-social-item {
            --zolo-button-text-color: #000;
            border-color: var(--zoloblocks-brand-color);
            color: var(--zolo-button-text-color);
            justify-content: flex-start;
            overflow: hidden;
            .zolo-social-icon {
                color: var(--zoloblocks-brand-button-color);
                background-color: var(--zoloblocks-brand-color);
            }
            &:hover {
                color: var(--zoloblocks-brand-button-color);
                background-color: var(--zoloblocks-brand-color-hover);
            }
        }

        .zolo-social-item.original {
            .zolo-social-icon {
                color: var(--zoloblocks-brand-button-color);
            }
            &.snapchat {
                .zolo-social-icon {
                    svg {
                        fill: #000;
                    }
                }
                &:hover {
                    .zolo-social-text {
                        color: #000;
                    }
                }
            }
            &:hover {
                .zolo-social-text {
                    color: var(--zoloblocks-brand-button-color);
                }
            }
        }
    }
